import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IArticulo, Response } from '../models/articulo';
import { AuthService } from '@core/auth/auth.service';
import { Response as rubrosResponse } from '../../rubros/models/rubro';
import { Response as umResponse } from '../../unidades-de-medida/models/unidad-de-medida';
import { IArticuloUnidadMedida, ResponseArticuloUm } from '../models/articulo-unidad-medida';

@Injectable()
export class ArticulosService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Articulos.
     * @returns Articulos
     */
    getArticulos(sort?: any): Observable<Response[]> {
        let f = '?expand=rubro&';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.stock.articulos}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token,
                    subject.next({
                        'list': resp.result,
                        // 'totalPages': resp.headers.get('x-pagination-page-count'),
                        // 'totalCount': resp.headers.get('x-pagination-total-count')
                    })
            });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param articulo 
     * @returns Crea un item nuevo
     */
    saveArticulo(articulo: IArticulo): Observable<IArticulo> {
        return this.http.post<IArticulo>(`${environment.stock.articulos}`, articulo);
    }

    /**
     * Funcion en el servicio
     * @param articulo
     * @returns Item actualizado actualizado
     */
    updateArticulo(articulo: IArticulo): Observable<IArticulo> {
        return this.http.put<IArticulo>(`${environment.stock.articulos}` + '/' + articulo.idarticulo, articulo);
    }

    /**
     * Funcion en el servicio
     * @param idarticulo 
     * @returns Elimina un item
     */
    deleteArticulo(idarticulo: number): Observable<IArticulo> {
        return this.http.delete<IArticulo>(environment.stock.articulos + '/' + idarticulo);
    }

    /**
     * 
     * @param idarticulounidadmedida 
     * @returns Elimina una Unidad de medida
     */
    deleteUnidadDeMedida(idarticulounidadmedida: number){
        return this.http.delete<IArticulo>(environment.stock.articulo_unidad_medidas + '/' + idarticulounidadmedida);
    }

    /**
     * 
     * @param unidad_de_medida 
     * @returns Guarda una unidad de medida asociada al articulo
     */
    saveUnidadDeMedida(unidad_de_medida: IArticuloUnidadMedida){
        return this.http.post<IArticuloUnidadMedida>(`${environment.stock.articulo_unidad_medidas}`,unidad_de_medida)
    }

    /**
     * Listamos los rubros para el selector del formulario de alta/modificacion
     * @returns Todos los rubros.
     */
    getRubros() {
        let f = '?filter[activo]=1'
        let subject = new Subject<any>();
        this.http.get<rubrosResponse[]>(`${environment.stock.rubros}${f}`)
            .subscribe((resp: any) => {
                subject.next({
                    'list': resp.result,
                })
            });
        return subject.asObservable();
    }

    /**
    * Funcion en el servicio
    * - Esta funcion retorna los unidades de medidas que tiene asociada un articulo.
    * @returns Unidades de medidas 
    */
    getUnidadesDeMedidaTable(articulo?: IArticulo): Observable<ResponseArticuloUm[]> {
        let f = `?filter[idarticulo]=${articulo.idarticulo}`;
        f += '&expand=um&sort=factor&';
        var subject = new Subject<any>();
        this.http.get<ResponseArticuloUm[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                subject.next({
                    'list': resp.result,
                })
            });
        return subject.asObservable();
    }

    /**
     * Listamos los valores para el selector del formulario de unidades de medida.
     * @returns 
     */
    getUnidadesDeMedida() {
        let f = '?filter[activo]=1';
        let subject = new Subject<any>();
        this.http.get<umResponse[]>(`${environment.stock.unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                subject.next({
                    'list': resp.result,
                })
            });
        return subject.asObservable();
    }
}