import { Component, Inject, OnInit } from '@angular/core';
import { IArticulo } from '../../models/articulo';
import { ArticulosService } from '../../service/articulos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { firstValueFrom } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table';
import { IUnidadDeMedida } from 'app/modules/stock/unidades-de-medida/models/unidad-de-medida';
import { IArticuloUnidadMedida } from '../../models/articulo-unidad-medida';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
@Component({
    selector: 'app-modal-unidades-medidas',
    templateUrl: './modal.component.html',
    styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnInit {
    columns = ['descripcion', 'factor', 'acciones'];
    form: FormGroup;
    title: string;
    articulo: IArticulo;
    articulo_um: IArticuloUnidadMedida;
    um: IUnidadDeMedida[]; //Para listar todas las uniades de medida en el selector.
    unidades_de_medida: IUnidadDeMedida[]; //Para listar unidades de medida asociadas a un articulo en la tabla.
    public dataSource: MatTableDataSource<IUnidadDeMedida> = new MatTableDataSource();
    sortValue: string;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private articulosService: ArticulosService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<ModalComponent>,
        private confirm: FuseConfirmationService,
    ) {
        this.articulo = this.data ? this.data : null;
        this.title = `UM de: ${this.articulo.descripcion} `;
    }

    ngOnInit(): void {
        this.newForm();
        this.getUnidadesDeMedida()
        this.getUnidadesDeMedidaTable();
    }

    newForm() {
        this.form = new FormGroup({
            unidad_medida: new FormControl((''), Validators.required),
            factor: new FormControl((''), [Validators.required, Validators.min(1), Validators.pattern('^[0-9]*$')]),
        });
    }

    /**
    * Esta funcion la utilizamos cuando queremos listar elementos en una tabla.
    */
    async getUnidadesDeMedidaTable() {
        const unidades_de_medida = await firstValueFrom(this.articulosService.getUnidadesDeMedidaTable(this.articulo))
        this.getDataUnidadesDeMedidasTable(unidades_de_medida);
    }

    async getDataUnidadesDeMedidasTable(data) {
        new Promise(async (resolve) => {
            this.unidades_de_medida = data.list
            this.dataSource = new MatTableDataSource(this.unidades_de_medida);
            resolve(true)
        })
    }

    /**
    * Esta funcion la utilizamos cuando queremos listar elementos en un selector.
    */
    async getUnidadesDeMedida() {
        const unidades_de_medida = await firstValueFrom(this.articulosService.getUnidadesDeMedida())
        this.getDataUnidadeDeMedida(unidades_de_medida);
    }

    async getDataUnidadeDeMedida(data) {
        new Promise(async (resolve) => {
            this.um = data.list
            resolve(true)
        })
    }

    deleteUnidadDeMedida(unidad_medida: IArticuloUnidadMedida) {
        this.confirm.open({
            title: "Eliminar UM " + unidad_medida.um.descripcion,
            message: "¿Desea eliminar la UM: " + unidad_medida.um.descripcion + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this.articulosService.deleteUnidadDeMedida(unidad_medida.idarticulounidadmedida).subscribe({
                        next: (res: any) => {
                            if (res.success == false) {
                                // Manejar el error
                                console.error('No se puede eliminar una UM con factor 1. Elimine las demas UM y vuelva a intentar.');
                                this.alert.error("No puede borrarse UM con factor 1. Elimine las demas UM y vuelva a intentar");
                                alert("No puede borrarse UM con factor 1. Elimine las demas UM y vuelva a intentar");
                            } else {
                                // Manejar la respuesta exitosa
                                this.alert.success("Articulo eliminado con éxito.");
                                this.getUnidadesDeMedidaTable()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.error("Error");
                        }
                    });
                }
            });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.articulo_um = {
                ...this.articulo_um,
                idarticulo: this.articulo.idarticulo,
                idunidadmedida: this.form.get('unidad_medida').value,
                factor: this.form.get('factor').value,
            }
            if (!this.articulo_um.idarticulounidadmedida) {
                this.articulosService.saveUnidadDeMedida(this.articulo_um)
                    .subscribe({
                        next: (res: any) => {
                            if (res.success) {
                                this.getUnidadesDeMedidaTable()
                            }else{
                                alert('La primera UM debe ser factor 1 y un articulo no puede tener 2 UM del mismo tipo.')
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}